<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl, formatDate } from "../utils/utils.js"
export default {
  name: 'SettlementFlow',
  data() {
    /**
   * 公共验证函数(验证数字值)
   * @params message - 提示信息
   */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证手机号)
    * @params message - 提示信息
    */
    function checkPhoneMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          callback()
        } else {
          var phone = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/
          if (phone.test(value)) {
            callback()
          } else {
            return callback(new Error(name + '填写有误'));
          }
        }
      }
    }

    /**
    * 公共验证函数(验证必填手机号)
    * @params message - 提示信息
    */
    function checkRequirePhoneMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          var phone = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/
          if (phone.test(value)) {
            callback()
          } else {
            return callback(new Error(name + '填写有误'));
          }
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      tableFields: { //导出Excel表格的表头设置
        "机器人数量": "robotCount",
        "发货进度": "type",
        "申请人": "applicantName",
        "发货工厂": "factoryName",
        "发货人": "consignorName",
        "收货景区": "receivingName",
        "收货人": "consigneeName",
        "收货人电话": "consigneePhone",
        "收货地址": "receiningAddress",
        "发货时间": "consignmentDate",
      },
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      dateStateOptions: [{
        value: '1',
        label: '按月查询'
      }, {
        value: '2',
        label: '按日查询'
      }, {
        value: '3',
        label: '按年查询'
      }], // 日期统计方式选项
      typeOptions: [{
        value: 1,
        label: '待发货'
      }, {
        value: 2,
        label: '已发货'
      }, {
        value: 3,
        label: '已收货'
      }, {
        value: 4,
        label: '已关闭'
      }], // 景区选项

      editTypeOptions: [{
        value: 1,
        label: '待发货'
      }, {
        value: 4,
        label: '已关闭'
      }], // 编辑时景区选项

      loadingTypeOptions: [
        {
          value: 1,
          label: '人工装车'
        }, {
          value: 2,
          label: '叉车装车'
        }
      ],

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },

      applicanteOptions: [], // 申请人选项      
      factoryOptions: [], // 工厂选项

      consignorOptions: [], // 发货人选项
      consigneeOptions: [], // 收货人选项
      consignmentDateOptions: [], // 调运时间选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示添加dialog对话框
      editDialogVisible: false, // 是否显示编辑dialog对话框
      detailDialogVisible: false, // 是否显示详情dialog对话框

