    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var spotId = this.searchVF.spotId
      if (spotId == "" || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginTime, endTime
      if (startEndDate == "" || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginTime = null
        endTime = null
      } else {
        beginTime = this.searchVF.startEndDate[0]
        endTime = this.searchVF.startEndDate[1]
      }

      var type = this.searchVF.type
      if (type == "" || !type) {
        type = null
      }

      const { data: res } = await this.$http.get("/system/factory_send/sendList", {
        params: {
          spotId: spotId,
          beginDate: beginTime,
          endDate: endTime,
          type: type,
        }
      })
      if (res.state == "200") {
        this.exportTableData = res.data
      } else {
        return
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.spotId == this.searchVF.spotId && this.selectDateState == this.searchSelectDateState && this.searchValidateForm.startEndDate == this.searchVF.startEndDate && this.searchValidateForm.type == this.searchVF.type) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.spotId = this.searchValidateForm.spotId
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate
      this.searchVF.type = this.searchValidateForm.type

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      this.initTableData()
    },

